.navbar {
  background: var(--secondary);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 1.071rem;
  padding-bottom: 1.071rem;
  z-index: 999;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  min-height: 3.571rem;
}

@media (min-width: 768px) {
  .navbar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.lang {
  margin-right: 1.786rem;
  display: flex;
}
.lang .lang-item {
  color: var(--primary);
}
.lang .lang-item:not(:first-child):before {
  content: "|";
  position: relative;
  top: -0.071rem;
}
.lang .lang-link {
  color: var(--primary);
  text-transform: uppercase;
  display: inline-block;
  padding: 0.357rem 0.714rem;
  transition: all 0.4s ease-in-out;
}
.lang .lang-link:is(:active, :hover, :focus, .active) {
  text-decoration: underline;
}
.lang .lang-link:is(.active) {
  font-weight: 600;
}

.menu-toggle {
  position: relative;
  padding: 0.714rem 0;
  z-index: 2;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.menu-toggle .bar {
  display: block;
  height: 0.143rem;
  background-color: var(--primary);
  transition: all 0.4s ease-in-out;
}
.menu-toggle .bar:first-child {
  width: 2.143rem;
  margin: auto;
}
.menu-toggle .bar:nth-child(2) {
  margin: 0.429rem 0 0.429rem 0.429rem;
  width: 1.714rem;
}
.menu-toggle .bar:last-child {
  width: 1.143rem;
  margin-left: 1rem;
}
.menu-toggle.toggle {
  margin: 0 -0.714rem;
}
.menu-toggle.toggle .bar:first-child {
  transform: rotate(45deg) translateY(11px);
}
.menu-toggle.toggle .bar:nth-child(2) {
  opacity: 0;
}
.menu-toggle.toggle .bar:last-child {
  transform: rotate(-45deg) translateY(-11px);
  width: 2.143rem;
  margin-left: 0;
}

.navbar .navbar-brand {
  position: relative;
  height: auto;
  font-size: 1.643rem;
  font-weight: 600;
  text-transform: capitalize;
  letter-spacing: 1px;
  color: var(--white);
  padding: 0.714rem 0;
  font-family: var(--font-montserrat);
}
@media (max-width: 767px) {
  .navbar .navbar-brand {
    display: block;
    text-align: center;
    padding: 0;
  }
}
.navbar .navbar-brand::after {
  content: "";
  display: inline-block;
  margin-left: 0.214rem;
  width: 0.357rem;
  height: 0.357rem;
  background-color: var(--primary);
}
.navbar span {
  color: var(--primary);
}

.navbar .navbar-links {
  visibility: hidden;
}
